

<template lang="html">
    <label class="notify-loading">
        <img :src="imgs[options.icon]" />
    </label>
</template>

<script>

export default {
    data() {
            return {
                timeout: '',
                imgs:['data:image/gif;base64,','data:image/gif;base64,','data:image/gif;base64,']
            }
        },
        props: {
            options: {
                type: Object,
                default: function() {
                    return {}
                }
            }
        },
        computed: {},
        mounted() {
          let self = this;

          setTimeout(function () {
              document.getElementById(self.options.id).remove();
                delete self.$layer.instances[self.options.id];
          }, self.options.time * 1000);
        },
        methods: {},
        watch: {},
        components: {}
}

</script>



